<template>
	<div style="" class="box">
		<div class="saveTime">
			实时保存，保存时间 13:52
		</div>
		<div class="list">
			<p>今日完成工作</p>
			<van-field v-model="message" rows="2" autosize type="textarea" placeholder="请输入" />
		</div>
		<div class="list">
			<p>未完成工作</p>
			<van-field v-model="message" rows="2" autosize type="textarea" placeholder="请输入" />
		</div>
		<div class="list">
			<p>需协调工资</p>
			<van-field v-model="message" rows="2" autosize type="textarea" placeholder="请输入" />
		</div>
		<div class="list">
			<p>备注</p>
			<van-field v-model="message" rows="1" autosize type="textarea" placeholder="请输入" />
		</div>
		<div class="split"></div>
		<div class="list upload">
			<p>
				图片
				<van-icon name="photo-o" size="1.7rem" />
			</p>
			<van-uploader style="margin-left: 10px;" :after-read="afterRead" multiple />
			<!-- <div class="wrapper" style="touch-action: none">
				<div class="container" v-dragula="fileList" bag="two-bag" style="overflow: hidden;">
					<div v-for="text in fileList" @click="onClick" style="width: 80px;float: left;">
						<img :src="text.content" alt="" style="width: 100%;">
					</div>
				</div>
			</div> -->
			<div class="mainDiv" id="columns">
				<div class="childDiv child" v-for="(option,index) in fileList" :key="index">
					{{option.content}}
					<img :src="option.content" alt="" style="width: 100%;">
				</div>
			</div>
		</div>
		<div class="list upload">
			<p>
				附件
				<!-- <van-icon name="photo-o" size="1.7rem" /> -->
			</p>
			<!-- <van-uploader v-model="fileList" style="margin-left: 10px;" :after-read="afterRead" multiple /> -->
			<div class="van-uploader" style="margin-left: 10px;">
				<div class="van-uploader__wrapper">
					<div class="van-uploader__upload" style="width: 40px;height: 40px;">
						<van-icon name="plus" />
						<input multiple="multiple" type="file" @change="afterRead" class="van-uploader__input">
					</div>
				</div>
			</div>
		</div>
		<bottom></bottom>
		<div style="height: 100px;"></div>
		<van-number-keyboard safe-area-inset-bottom />
	</div>
</template>

<script>
	import bottom from '../../components/workSummaryBottom/index.vue'
	export default {
		components: {
			bottom
		},
		data() {
			return {
				message: '',
				columns: undefined,
				flags: false,
				position: {
					x: 0,
					y: 0
				},
				nx: '',
				ny: '',
				dx: '',
				dy: '',
				xPum: '',
				yPum: '',
				fileList: [
					// {
					// 	url: 'https://img.yzcdn.cn/vant/leaf.jpg',
					// 	status: 'uploading',
					// 	message: '上传中...',
					// },
					// {
					// 	url: 'https://img.yzcdn.cn/vant/tree.jpg',
					// 	status: 'failed',
					// 	message: '上传失败',
					// },
				],
			}
		},
		methods: {
			afterRead(file) {
				console.log(file,'filesasdsadsadsa')
				if (file.length) {
					file.forEach((item) => {
						this.fileList.push(item)
					})
				} else {
					this.fileList.push(file)
				}
				console.log(this.fileList,'this.fileList')
				this.columns = document.querySelectorAll('.child');
				console.log(this.columns)
				let num = 0;
				for (let i of this.columns) {
					i.style.top = (i.offsetHeight * num) + 'px';
					i.addEventListener('touchstart', this.down);
					i.addEventListener('touchmove', this.move);
					i.addEventListener('touchend', this.end);
					num++;
				}
				// file.status = 'uploading';
				// file.message = '上传中...';

				// setTimeout(() => {
				// 	file.status = 'failed';
				// 	file.message = '上传失败';
				// }, 1000);
			},
			onClick(e) {
				console.log(e)
			},
			down(e) {
				e.preventDefault();
				this.flags = true;
				var touch;
				if (e.touches) {
					touch = e.touches[0];
				} else {
					touch = e;
				}
				/*touch.clientX clientY  鼠标点击的位置与视图窗口的距离
				 *  e.target.offsetLeft offsetTop  鼠标点击的div与父元
				 * 素的边框距离，父元素必须为定位样式，不然认为父元素为body
				 * */
				this.position.x = touch.clientX;
				this.position.y = touch.clientY;
				this.dx = e.target.offsetLeft;
				this.dy = e.target.offsetTop;
			},
			move(e) {
				if (this.flags) {
					var touch;
					if (e.touches) {
						touch = e.touches[0];
					} else {
						touch = e;
					}
					this.nx = touch.clientX - this.position.x;
					this.ny = touch.clientY - this.position.y; //移动的距离
					this.xPum = this.dx + this.nx;
					this.yPum = this.dy + this.ny;
					e.target.style.left = this.xPum + 'px';
					e.target.style.top = this.yPum + 'px';
				}
			},
			end(e) {
				//处理边界问题
				let right = e.target.offsetLeft + e.target.offsetWidth;
				let bottom = e.target.offsetTop + e.target.offsetHeight;
				if (e.target.offsetLeft <= 0 || right >= e.target.offsetParent.offsetWidth) {
					e.target.style.left = 0 + 'px';
				}
				if (e.target.offsetTop <= 0 || bottom >= e.target.offsetParent.offsetHeight) {
					e.target.style.top = 0 + 'px';
				}
				this.dataTransfer(e);
				this.flags = false;
			},
			dataTransfer(e) {
				let eleTop = e.target.offsetTop + Math.round(e.target.offsetHeight / 2); //找到当前元素的中间位置
				let arr = Array.from(this.columns); //将nodelist转为array
				let index = arr.indexOf(e.target); //找到当前元素下标
				for (let i in arr) {
					//如果当前元素进入另一个元素的位置，将他们的值互换，位置还原
					if (eleTop > arr[i].offsetTop && eleTop < (arr[i].offsetTop + arr[i].offsetHeight)) {
						//值互换，位置还原（保证数组的序列数据不变）
						console.log(arr[index].innerText,'arr[index].innerText')
						let temp = arr[index].innerText;
						arr[index].innerText = arr[i].innerText;
						arr[i].innerText = temp;
					}
				}
				let num = 0;
				for (let i of this.columns) {
					i.style.top = (i.offsetHeight * num) + 'px';
					num++;
				}
			}
		},
	}
</script>

<style scoped>
	.saveTime {
		padding: 5px;
		text-align: right;
		font-size: 14px;
		color: rgba(0, 0, 0, 0.4);
	}

	.box {
		width: 100%;
		overflow-x: hidden;
		background-color: #F8F8F8;
		height: 100vh;
	}

	.list {
		width: 100%;
		text-align: left;
		/* border:1px  solid red; */
		border-bottom: 1px solid rgba(0, 0, 0, 0.08);
		background-color: white;
	}

	.list p {
		margin: 0;
		font-size: 16px;
		padding: 10px 10px 0;
		background-color: white;
		font-weight: 400;
	}

	.split {
		height: 20px;
	}

	.upload p {
		padding: 10px 10px 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.mainDiv {
		position: absolute;
		height: 500px;
		width: 95%;
		border: 3px solid red;
		border-radius: 10px;
		margin: 10px;
	}

	.mainDiv>.childDiv {
		position: absolute;
		height: 50px;
		width: 90%;
		/* background-color: blue; */
		border: 2px solid;
		border-radius: 10px;
		margin: 1px auto;
		padding: 10px;
		text-align: center;
	}


	.test {
		position: relative;
		height: 50px;
		width: auto;
		background-color: red;
		border: 2px solid;
		border-radius: 3px;
		margin: 1px 0 1px;
		padding: 10px;
		text-align: center;
	}
</style>
